
.work-image{
    width: 400px;
    position: relative;
    display: block;
}

.work-content {
    height :70vh;
    // width: 80px;
    .text-content{
        position: absolute;
        width: 100%;
        top:50%;
        left: 100%;
        padding: 4px 15px;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 50px;
    }
}

.work-sub-heading-wrapper{
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;

    .work-sub-heading{
        font-size: 40px;
        font-weight: 500;
        letter-spacing: 4px;
        text-transform: uppercase;
        // padding: 10px 0px;
        padding-bottom: 0px;
        // font-weight: 100;
        font-family: $font-plex;
        text-shadow:-1px 0px 0px #1AD7E0, -3px 0px 0px #0D10DC,1px 0px 0px #d68010 ,3px 0px 0px #CB0C0C;
    }

    .sub-heading-side{
        color:$gray-600;
    }
}


.test{
    font-size: 12px;
    max-width: 300px;
    text-transform: uppercase;
}

.work-item-wrapper{
    width: 100%;
    position: relative;
    padding: 30px 0px;

    .image-banner{
        border-radius: 4px;
        // width: 86%;
        width: 100%;
        display: block;
        position: relative;
        height: 650px;
        object-fit: cover;
    }
    
    .text-title{
        position: absolute;
        top:45%;
        left:4%;
        font-size: 25px;
        font-weight: 500;
        text-transform: uppercase;
        display: flex;
        justify-content: space-between;
        width: 92%;
        // transform: translate(-50%, -50%);

        .project-year{
            font-size: 15px;
            font-weight: 500;
            text-transform: uppercase;
            line-height: 20px;

        }
        .project-types{
            font-size: 15px;
            font-weight: 500;
            text-transform: uppercase;
            line-height: 20px;

        }
        .project-desc{
            max-width: 300px;
            font-family: $font-ptmono;
            font-size: 13px;
            line-height: 14px;
            font-weight: 500;
            text-transform: uppercase;

            .project-link{
                padding-top: 20px;
                font-weight: 600;
                font-family: $font-plex;
                text-decoration: underline;
            }

        }

    }

    .text-sub{
        position: absolute;
        top:45%;
        left:50%;
        font-size: 15px;
        font-weight: 500;
        text-transform: uppercase;
        line-height: 20px;
        // transform: translate(-50%, -50%);
    }
    .text-desc{
        position: absolute;
        top:45%;
        left:80%;
        font-size: 13px;
        line-height: 14px;
        font-weight: 500;
        text-transform: uppercase;
        // transform: translate(50%, 50%);
        max-width: 300px;
        font-family: $font-ptmono;

        .project-link{
            padding-top: 20px;
            font-weight: 600;
            font-family: $font-plex;
            text-decoration: underline;
        }
    }

        .project-sub-data{
            // max-width: 300px;
            font-family: $font-ptmono;
            font-size: 13px;
            line-height: 14px;
            font-weight: 500;
            text-transform: uppercase;
            display: flex;
            justify-content: space-between;
            color:$gray-600;

        }

}